import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Alert } from "react-bootstrap";

import styled from "styled-components";

const 박스 = styled.div`
  padding: 20px;
`;
const 제목 = styled.h4`
  font-size: 30px;
`;

function Detail(props) {
  const navigate = useNavigate();
  const { id } = useParams();
  let [재고없음, set재고없음] = useState(true);

  // let findItem = props.shoes.find((x) => x.id === id);

  useEffect(() => {
    let 타이머 = setTimeout(() => set재고없음(false), 2000);

    return () => {
      clearTimeout(타이머); // 이걸 꼭 넣어줘야 2초전에 다른페이지로 넘언가도 버그가 안 생김.
      // alert("재고소진!");
    };
  }, [재고없음]);
  // 대괄호에 들어가는애는 특정 state가 변할때만 실행해주세요~라는 의미 -> 재고가 변할때마다 실행됨.
  // 대괄호가 빈칸이면 로딩될때만 실행해주세요.
  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
      </박스>
      {재고없음 === true ? <Alert className="alert alert-danger">재고가 얼마 남지 않았습니다</Alert> : null}
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes" + (Number(id) + 1) + ".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{props.shoes[id].title}</h4>
          <p>{props.shoes[id].content}</p>
          <p>{props.shoes[id].price}</p>
          <Remain remain={props.remain} id={id}></Remain>
          <button
            className="btn btn-danger"
            onClick={() => {
              let newRemain = [...props.remain];
              newRemain[id] = newRemain[id] - 1;
              props.setRemain(newRemain);
              // console.log(newRemain[id]);
              // console.log(id);
            }}
          >
            주문하기
          </button>
          <button
            className="btn btn-secondary"
            onClick={() => {
              navigate(-1);
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Remain(props) {
  return <p>재고 : {props.remain[props.id]}</p>;
}

export default Detail;
